import styled from 'styled-components'
import Link from 'next/link'
import BannerHeader from './Header';

const Truncate = styled.p`
  height: 50%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`

const List = styled.li`
  text-decoration: None;
`
class AllTenders extends React.Component {
    render() {
      return (
        <div className="row">
          {this.props.tenders.map((data) => (
            <div className="col-md-6 mb-6" key={data.public_id}>
              <div className="card h-100">
                <div className="card-body">
                  <h2 className="card-title">{data.title}</h2>
                  <Truncate className="card-text">{data.description}</Truncate>
                  <ul>
                    <List>Application Close Date : {data.application_close_date}</List>
                    <List>Application Start Date : {data.application_start_date} </List>
                  </ul>
                </div>
                <div className="card-footer">
                  <Link as={`/tenders/${data.public_id}`} href={`/tenders?public_id=${data.public_id}`}>Read More</Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      );
    }
  }
  
  export default AllTenders;